:root {
  --border-bottom: 4px solid #E0354B;
  --padding: 1.2em;
}

.App {
  height: 100vh;
  max-width: 100vw;
}

.App-header {
  color: #E0354B;
  display: flex;
  align-items: center;
  padding: var(--padding);
  border-bottom: var(--border-bottom);
}

.App-header img {
  width: 250px;
  height: 250px;
}

.App-body {
  padding: calc(var(--padding) * 2);
  border-bottom: var(--border-bottom);
  height: 45%;
}

.App-body #email, .App-body #password {
  margin-right: 1em;
}

.App-footer {
  padding: var(--padding);
  text-align: center;
  font-style: italic;
}